export function requestPost(config) {
  // promise
  // axios
  const instance = axios.create({
    // headers: {
    //   'Content-Type': 'application/x-www-form-urlencoded'
    // },
    // 
    method: 'post',
    // 
    // baseURL: 'http://192.168.1.15:8080/idevcloudweb'
    // baseURL: 'http://122.112.160.41:8080/amk/'
    baseURL: BASE_URL
    // 
    // timeout: 1000
  });

  // promise
  return instance(config);
};
