export function getUrlKeyMain(name, url) {
  // eslint-disable-next-line no-sparse-arrays
  return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
};

export function autoLoginFunc(next) {
  var yh = '';
  var pw = '';

  var psw = getUrlKeyMain('autoLogin', window.location.href);
  // var psw = Crypto.encryptText(this.loginAccount + '_' + this.loginPassword, '12345678fbs123abcdefg');
  // console.log('', psw);
  var jsw = Crypto.decryptText(psw, '12345678fbs123abcdefg');
  // console.log('', jsw);
  yh = jsw.split('_')[0];
  pw = jsw.split('_')[1];

  login({}, {
    userName: yh,
    password: pw
    // password: Crypto.encryptDes2(this.loginPassword, key, '12345678abcdefgh'),
    // loginTime: loginTime
  }).then(res => {
    console.log(res);
    if (res.code === '0000') {
      localStorage.setItem('token', JSON.stringify(res.rows.token));
      localStorage.setItem('realName', JSON.stringify(res.rows.realName));
      localStorage.setItem('userId', JSON.stringify(res.rows.userId));
      localStorage.setItem('account', JSON.stringify(res.rows.userName));
      localStorage.setItem('company', res.rows.companyName);
      localStorage.setItem('companyId', res.rows.companyId);
      localStorage.setItem('mesToken', res.rows.mesToken);
      localStorage.setItem('mesTokenHeader', res.rows.mesTokenHeader);
      localStorage.setItem('isBoss', res.rows.isBoss);
      next();
    }
  });
};

// 
export function accessLocalhost(allUrl, tokenUse, next) {
  var supplier = getUrlKeyMain('supplier', allUrl);
  console.log('', supplier);
  // 
  if (supplier && supplier === 'supos') {
    // supos/authorize
    localStorage.setItem('isSupplier', true);
    // window.location.href = BASE_URL + 'supos/authorize?redirectUrl=' + allUrl;
    window.location.href = 'http://122.112.160.41:8094/supos/' + 'supos/authorize?redirectUrl=' + allUrl;
  } else if (supplier && supplier === 'suposHosted') { // 
    // auth/authorize
    localStorage.setItem('isSupplier', true);
    // window.location.href = BASE_URL + 'supos/authorize?redirectUrl=' + allUrl;
    window.location.href = SUPOS_URL + 'auth/authorize?redirectUrl=' + allUrl;
  } else {
    // token
    if (tokenUse) {
      console.log(',token', tokenUse);
      next();
    } else {
      console.log(',token', tokenUse);
      next('/login');
    }
  }
};

// 
export function accessMk(allUrl, tokenAll, next) {
  console.log('token', tokenAll, allUrl);
  var mkToken = getUrlKeyMain('token', allUrl);
  loginMk({ token: mkToken }).then(res => {
    console.log('token', res);
    if (res.code === '0000') {
      console.log('token', res);
      // this.$store.state.isSupplier = true;
      localStorage.setItem('isSupplier', true);
      localStorage.setItem('token', res.rows.token);
      localStorage.setItem('account', res.rows.userName);
      localStorage.setItem('userId', res.rows.userId);
      localStorage.setItem('realName', res.rows.realName);
      localStorage.setItem('company', res.rows.companyName);
      localStorage.setItem('companyId', res.rows.companyId);
      next();
    } else {
      console.log('token', res);
      next('/login');
    }
  }).catch(() => {
    next('/login');
  });
};

