// Date prototype is read only, properties should not be added
/*
  eslint no-extend-native: ["error", { "exceptions": ["Date"] }]
*/
Date.prototype.format = function(fmt) {
  var o = {
    'M+': this.getMonth() + 1, // 
    'd+': this.getDate(), // 
    'h+': this.getHours(), // 
    'm+': this.getMinutes(), // 
    's+': this.getSeconds(), // 
    'q+': Math.floor((this.getMonth() + 3) / 3), // 
    S: this.getMilliseconds() // 
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length));
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
    }
  }
  return fmt;
};
/*
 * toJSONJSON.stringifytoJSON8toJSON
 */
Date.prototype.toJSON = function() {
  return this.format('yyyy-MM-dd hh:mm:ss'); // util.formatDate
};
/**
 * ptype'date''string'
 * @param {string} field 
 * @returns {string}
 */
export function getPtype(field) {
  return /Date$/.test(field) || /Time$/.test(field) ? 'date' : 'string';
}
