 * @Descripttion: vxe-model
 * @version:
 * @Author: YZZ
 * @Date: 2021-11-03 09:34:32
 * @LastEditors: ZJM
 * @LastEditTime: 2022-01-04 11:49:31
-->
<template>
  <div class="model-container">
    <vxe-modal
      v-model="visible"
      :id="modalID"
      :ref="modalID"
      :width="width"
      :height="height"
      :min-width="minWidth"
      :min-height="minHeigt"
      :show-zoom="showZoom"
      :resize="resize"
      :remember="remember"
      :storage="storage"
      :transfer="transfer"
      :showHeader="showHeader"
      :showFooter="showFooter"
      :mask="mask"
      :mask-closable="maskClosable"
      :margin-size="marginSize"
      :lock-view="lockView"
      :destroy-on-close="destroyOnClose"
      @show="dialogOpened"
      @close="closeModal"
    >
      <!--  -->
      <template #title>
        <slot name="title">
          <span class="model-title">{{ modalName }}</span>
        </slot>
      </template>
      <!--  -->
      <template #default>
        <slot name="default"></slot>
      </template>
      <!--  -->
      <template #footer>
        <slot name="footer" class="button-row"></slot>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
export default {
  name: 'modalVxeModal',
  data() {
    return {
      visible: this.show
    };
  },
  props: {
    // ID
    modalID: {
      type: String
    },
    // 
    modalName: {
      type: String,
      required: true
    },
    // 
    show: {
      type: Boolean,
      default: false
    },
    // 
    width: {
      type: [Number, String],
      default: '45%'
    },
    // 
    height: {
      type: [Number, String],
      default: '40%'
    },
    // 
    minWidth: {
      type: [Number, String],
      default: 0
    },
    // 
    minHeigt: {
      type: [Number, String],
      default: 0
    },
    // 
    showZoom: {
      type: Boolean,
      default: false
    },
    // 
    resize: {
      type: Boolean,
      default: false
    },
    // 
    remember: {
      type: Boolean,
      default: false
    },
    //  localStorage  id
    storage: {
      type: Boolean,
      default: false
    },
    //  body 
    transfer: {
      type: Boolean,
      default: false
    },
    // 
    showHeader: {
      type: Boolean,
      default: true
    },
    // 
    showFooter: {
      type: Boolean,
      default: true
    },
    // 
    mask: {
      type: Boolean,
      default: false
    },
    // 
    maskClosable: {
      type: Boolean,
      default: false
    },
    //  resize 
    marginSize: {
      type: Number,
      default: -600
    },
    // 
    lockView: {
      type: Boolean,
      default: false
    },
    // 
    destroyOnClose: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    /**
     * @description: 
     * @param {type}
     * @return:
     * @author: YZZ
     */
    closeModal() {
      // this.$emit('update:show', false);
      this.$emit('close');
    },

    dialogOpened() {
      // this.closeModal();
      this.$emit('opened');
    }
  },
  watch: {
    show() {
      this.visible = this.show;
    }
  },
  created() {
    // console.log('show', this.show);
    // console.log('id', this.modalID);
  }
};
</script>

<style lang="less" scoped>
.model-container {

}
::v-deep.vxe-modal--wrapper .vxe-modal--header {
  background-color: #fff;
}

.model-title {
  border-left: 4px solid #0871DC;
  padding-left: 8px;
  font-weight: bold;
}

// 
::v-deep.vxe-modal--wrapper .vxe-modal--body {
  padding-bottom: 0;
}

// 
::v-deep.vxe-modal--wrapper .vxe-modal--footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-top: 0;
  // button
  .el-button:not(:last-child) {
    margin-right: 30px;
  }
}
</style>
