import Vue from 'vue';
/**
 * @description: msgtitletype
 * @param {String} msg 
 * @param {String} title 
 * @param {String} type warning
 * @param {Array} settings 
 * @return:
 * @author: YZZ
 */
Vue.prototype.showConfirm = function alert(msg = '', title = '', type = 'warning', settings = {}) {
  Object.assign(settings, { // 
    confirmButtonText: '',
    cancelButtonText: '',
    type: type,
    dangerouslyUseHTMLString: true // html
  });
  return this.$confirm(msg, title, settings);
  // return this.$confirm(`<p style="font-weight:bold;">${msg}</p>`, title, settings);
};

