// let Validation = {};
exports.install = function(Vue, options) {
  /**
   * @description: 
   * @param {Array} item 
   *  item
   *    required true 
   *    name 
   *    maxLength 
   *    minmax  min < max number
   *    type  phone
   *          IDCard
   *
   * @return:
   * @author: YZZ
   */
  Vue.prototype.filter_rules = function(item) {
    const rules = [];
    if (item.required) {
      rules.push({ required: true, message: item.name + '!', trigger: 'blur' });
    }
    if (item.maxLength) {
      rules.push({ min: 1, max: item.maxLength, message: '' + item.maxLength + '', trigger: 'blur' });
    }
    if (item.min && item.max) {
      rules.push({ min: item.min, max: item.max, message: '' + item.min + '' + item.max + '', trigger: 'blur' });
    }
    if (item.type) {
      const type = item.type;
      switch (type) {
        case 'phone':
          // 
          rules.push({ pattern: /(^(\d{3,4}-)?\d{7,8})$|(^1[34578]\d{9})$/, message: '', trigger: 'blur' });
          break;
        case 'IDCard':
          // 
          rules.push({ pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/, message: '', trigger: 'blur' });
          break;
        default:
          rules.push({});
          break;
      }
    }
    return rules;
  };
};
