import Vue from 'vue';
import XEUtils from 'xe-utils';//  xe-utilsthis.$utils
import VXETable from 'vxe-table';
import VXETablePluginElement from 'vxe-table-plugin-element';
import 'vxe-table/lib/index.css';
import 'vxe-table-plugin-element/dist/style.css';

Vue.use(VXETable);
VXETable.use(VXETablePluginElement);

// 
VXETable.formats.mixin({
  // 
  formatSex({ cellValue }) {
    return cellValue ? (cellValue === '1' ? '' : '') : '';
  },
  // 
  formatSelect({ cellValue }, list) {
    const item = list.find(item => item.value === cellValue);
    return item ? item.label : '';
  },
  //  yyyy-MM-dd HH:mm:ss
  formatDate({ cellValue }, format) {
    return XEUtils.toDateString(cellValue, format || 'yyyy-MM-dd HH:mm:ss');
  },
  // 2
  formatAmount({ cellValue }, digits) {
    return XEUtils.commafy(cellValue, { digits: digits || 2 });
  },
  // 4
  formatBankcard({ cellValue }) {
    return XEUtils.commafy(cellValue, { spaceNumber: 4, separator: ' ' });
  },
  // ,
  formatFixedNumber({ cellValue }, digits) {
    return XEUtils.toNumber(cellValue).toFixed(digits || 2);
  },
  // ,
  formatCutNumber({ cellValue }, digits) {
    return XEUtils.toFixedString(cellValue, digits || 2);
  },
  //  moment 
  toMomentString(cellValue, format) {
    return cellValue ? cellValue.format(format) : '';
  },
  // 
  timeStamp({ cellValue }) {
    cellValue = parseInt(cellValue / 1000 / 60);
    var day = parseInt(cellValue / 60 / 24);
    var hour = parseInt(cellValue / 60 % 24);
    var min = parseInt(cellValue % 60);
    cellValue = '';
    if (day > 0) {
      cellValue = day + '';
    }
    if (hour > 0) {
      cellValue += hour + '';
    }
    if (min > 0) {
      cellValue += parseFloat(min) + '';
    }
    return cellValue;
  },
  // 
  timeStampSeconds({ cellValue }) {
    cellValue = cellValue / 1000;
    var day = parseInt(cellValue / 60 / 60 / 24);
    var hour = parseInt(cellValue / 60 / 60 % 24);
    var min = parseInt(cellValue / 60 % 60);
    var s = parseInt(cellValue % 60);
    cellValue = '';
    if (day > 0) {
      cellValue = day + '';
    }
    if (hour > 0) {
      cellValue += hour + '';
    }
    if (min > 0) {
      cellValue += parseFloat(min) + '';
    }
    if (s > 0) {
      cellValue += parseFloat(s) + '';
    }
    return cellValue;
  }
});
