import { get, postParamJSON } from '@/utils/request';

// fieldtype
export const getDropDownListForSearchAPI = async query => {
  const res = await postParamJSON('/configure/pageSearch/loadPageSearch.action?', query, {});
  res.characterDropDown2 = res.characterDropDown;
  res.characterDropDown = res.characterDropDown.string;
  return new Promise((resolve, reject) => {
    resolve(res);
  });
};

// api
export const getDropDownListByAPI = async url => get(
  url,
  {},
  {}
);

